
#icons {
    display: flex;
    justify-content: start;
    transition: all 0.2s ease-in-out 0s;
}

#icons a {
    transition: all 0.2s ease-in-out 0s;
    margin-right: 10px;
}

#icons a img {
    height: 30px;
    width: 30px;
    border-radius: 5px;
    transition: all 0.2s ease-in-out 0s;
}

#icons a img:hover {
    transform: scale(1.1);
    border-radius: 15px;
}

@media (max-width: 780px) {
    #icons {
        justify-content: center;
    }
}