#notif {
    font-family: 'Inter';
    font-size: 15px;
    background: rgba(228, 56, 26, 0.9);
    color: white;
    border: none;
    display: flex;
    padding: 10px;
    justify-content: center;
    max-width: 170px;
    margin-top: 10px;
    transition: all 0.5s ease 0s;
}

#notif:hover {
    font-weight: 700;
    border-radius: 10px;
    transform: translate(0, 1px);
    box-shadow: 0 0 10px rgb(201, 29, 17);
}

@media (max-width: 780px) {
    #notif {
        margin-left: auto;
        margin-right: auto;
    }
}
