
form {
    display: flex;
    justify-content: center;
}

input {
    margin-bottom: 1px;
    border: none;
    width: 200px;
    border-radius: 5px;
    margin-top: 30px;
    font-size: 19px;
    font-family: 'Inter';
    padding: 10px;
    transition: 0.7s;
}

input:focus {
    color: #212529;
    background-color: #fff;
    border-color: #bdbdbd;
    outline: 0;
    box-shadow: 0 0 0 0.2rem rgba(158, 158, 158, 0.25);
}

input:hover {
    box-shadow: 0 0 5px lightblue;
}

#prvt {
    position: relative;
    border: none;
    font-family: 'Inter';
    font-weight: 900;
    font-size: 10px;
    height: 22px;
    width: 22px;
    background: #616466;
    border: none;
    transform: scale(2);
    transition: all 0.2s ease-in 0s;
    left: 20px;
    padding: 0;
    top: 40px;
}

#prvt:hover {
    border-radius: 8px;
    transform: scale(2.2);
}

#prvt:active {
    transform: scale(2);
    outline: 0;
}

#last_ct{
    display: flex;
    color: grey;
    min-width: 130px;
    justify-content: center;
    margin: 10px auto;
    font-size: 15px;
    border-radius: 5px;
    font-family: 'Inter';
    font-weight: 900;
    border: none;
    padding: 5px 15px;
}

#last_ct:focus {
    outline: 0;
    box-shadow: 0 0 0 0.18rem rgba(158, 158, 158, 0.25);
}
