@import url('https://fonts.googleapis.com/css2?family=Inter:wght@200&display=swap');

body {
    color: white;
    padding: 30px;
    font-family: 'Inter';
}

#weather {
    margin-top: 30px;
    display: flex;
    justify-content: center;
}

#weather__container {
    line-height: 20px;
    text-align: start;
    display: flex;
    justify-content: space-between;
    /* background: linear-gradient(to top left, rgba(26, 154, 204, 0.9), rgba(255, 137, 137, 0.9)); */
    background: linear-gradient(to top left, rgba(228, 56, 26, 0.9), rgba(18, 161, 172, 0.9));
    width: 260px;
    padding: 20px;
    border-radius: 30px;
}

#right-part img{
    margin-top: -10px;
    margin-left: 40px;
    height: 70px;
    width: 70px;
}

#left-part p {
    line-height: 25px;
    margin: 0;
}

#left-part p:nth-child(1) {
    margin-bottom: 10px;
    font-family: sans-serif;
}

#title__city {
    font-size: 22px;
    font-weight: 700;
}

#links div{
    position: relative;
    left: 112px;
}

#link__icon {
    transition: all 0.3s ease 0s;
}

#link__icon:hover {
    transform: scale(1.1);
}

#tooltip {
    font-size: 12px;
    position: relative;
    left: 100px;
    color: black;
    font-weight: 900;
    bottom: 45px;
    background-color: rgba(255, 255, 255, 0.7);
    padding: 10px;
}

@media (max-width: 640px) {
    #tooltip {
        left: -70px;
        bottom: 65px;
    }
}